import React from 'react';
import './App.css';
import { Link} from "react-router-dom";


class More extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      message: "",
      user: '',
      contractInstance: '',
      user:'',
      networkId:"",
      runesRemainingToAssign:0,
      myRuneCounter:0,
      myRunes:[]
    };
  }

  componentWillMount(){
    this.setState({
      hasData:true
    })
  }
  componentWillUnmount(){
    window.scrollTo(0, 0);
  }


  render() {

    if (this.state.hasData) {
      return (
        <div className="App"   >
          <section >
            <div >
              <div>
              <h1>
                Contract updated
              </h1>
              <p>
                The first version of the contract didn't follow the ERC721 token standard. 
                <br />
                <br /> 
                A new contract has been created at <b>0x48581d582fb10ee92bb82fb72d52b921b4ca49d4</b>. 
                <br />
                <br />
                The new contract follows the ERC721 standard with the ERC721URIStorage interface, <br />as well as images uploaded to IPFS.
                <br />  <br />
                Everyone that held crypto runes until the creation of the new contract,<br /> got airdropped new runes for free. 
              
              </p>
     
              <br/>
              Go back to the <Link to="/details">details page</Link>
              <br/>
              <br/>
              Go back to the <Link to="/">homepage</Link>
              </div>
              </div>
          </section>
        </div>
      );
    } else {
      return (
        <div className="App" >
          <section >
          <div className="sk-folding-cube">
  <div className="sk-cube1 sk-cube"></div>
  <div className="sk-cube2 sk-cube"></div>
  <div className="sk-cube4 sk-cube"></div>
  <div className="sk-cube3 sk-cube"></div>
</div>
          </section>
        </div>
      );
    }

  }
}

export default More;
